/**
 *
 * @param {*} _8d
 */
pim.getProductDetail = async (_8d) => {

    "use strict";
    let url = api.search.findPdpPath();

    let pd = {};
    try {
        let res = await http.get(url + _8d);
        if (res.data && res.data[0] && res.data[0].result) {

            let path = res.data[0].result;

            let pdpurl = api.search.findPDP() + path;

            let dt = await http.get(pdpurl);

            if (dt.data && dt.data.resultData) {



                pd = dt.data.resultData;
                pd.hasDetail = true;

                if (pd.specifications && pd.specifications.length > 0) {
                    pd.specifications.forEach(element => {
                        if (element.eightd && element.eightd.indexOf(_8d) > -1) {

                            pd.currentBundle = element;
                        }
                    });
                }
            }

            //TODO:

        }

    } catch (err) {
        console.log(err);
    }

    return pd;

}


/**
 * product.mapOne
 * product.mapOne is map,that contains sub maps.
 * So you can consider, mapOne is level one fitler and the related sub maps are level two's
 * these two level maps for fitler
 *
 *
 */
pim.getMaster = async (_8d, _needDomain) => {

    "use strict";
    let pd = {};
    let url = api.product.findMasterData();

    try {

        let res = await http.get(url, { params: { eightD: _8d } });
        if (pim._validateRes(res)) {
            if (res && res.data && res.data.returnData && res.data.returnData.OutMasterData) {
                pd = res.data.returnData.OutMasterData;
            }
        }

        // alert(Domain)
        if (_needDomain && pd.colorImage && pd.colorImage.indexOf("http") < 0) {

            pd.colorImage = Domain + pd.colorImage;
        }

        if (_needDomain && pd.imagePath && pd.imagePath.indexOf("http") < 0) {
            pd.imagePath = Domain + pd.imagePath;
            // alert(pd.imagePath)
        }

        if (pd.filterLevelOneRefs && pd.filterLevelOneRefs.length > 0) {
            let mapOne = new Map();
            pd.mapOne = mapOne;

            pd.filterLevelOneRefs.forEach(one => {
                if (!mapOne.has(one.cnName)) {
                    mapOne.set(one.cnName, new Map());
                }

                let mapTwo = mapOne.get(one.cnName);

                Optional.ofNullable(one.filterLevelTwoRefs).filter(two => {
                    return two.length > 0;
                }).ifPresent(two => {
                    two.map(me => {
                        Optional.ofNullable(me).filter(m => pim._checkPro(m, "cnName")).filter(m => {
                            return me.isChecked && me.isChecked === "true";
                        }).ifPresent(me => {
                            mapTwo.set(me.cnName, true);
                        })
                    })
                });

            });

            // console.log("PD work is ",JSON.stringify(pd));


        }



    } catch (err) {
        console.log(err);
    }

    return pd;

}


/**
 * Get the product master data from PIM, as well as the member price
 * Notice:
 * pd.imagePath, is for product image on AEM
 * pd.srp, the SRP price
 * pd.price, the member price
 *
 * @param {*} _8d the product 8D code,that is mandory
 * @param {*} _group the member group
 */
pim.getMasterWithPrice = async (_8d, _group) => {

    "use strict";
    let pd = {};
    let url = api.product.findMasterData();



    try {

        pd = await pim.getMaster(_8d);

        let gp = "";
        if (_group && _group != "") {
            gp = _group;
        } else {


          switch (localStorage.getItem("member_group")) {
            case 'registergroup':
              gp = 'ZT01';
              break;
            case 'bronzegroup':
              gp = 'ZT02';
              break;
            case 'silvergroup':
              gp = 'ZT11';
              break;
            case 'goldgroup':
              gp = 'ZT12';
              break;
            default:
              break;

          }
        }

        // console.log("member_group is ", gp);
        //console.log("Product SKU 8D is ",JSON.stringify(pd));

        if (pd.sku8D) {

            let prcAPI = api.b2c.singlePrice();

            var ps = {
                eightcode: _8d,
                channel: "WAP",
                basestore: "SS",
                pval: gp
            };

            var pcs = await http.post(prcAPI, JSON.stringify(ps));

           // Optional.ofNullable(pcs).filter(pim._checkPro)

            if (pcs && pcs.data && pcs.data.returncode == "200") {

                if (pcs.data.data && pcs.data.data.productprice) {
                  pd.productprice = pcs.data.data.productprice;
                    if (pcs.data.data.productprice.srp) {
                        pd.srp = pcs.data.data.productprice.srp;
                    }

                    if (pcs.data.data.productprice.price) {
                        pd.price = pcs.data.data.productprice.price;
                    }
                    if (pcs.data.data.cartpromotelist) {
                        pd.cartpromotelist = pcs.data.data.cartpromotelist;
                    }

                }
            }
            // console.log("price is ",JSON.stringify(pcs));

        }




    } catch (err) {
        console.log(err);
    }

    return pd;



}

