<script>

import CommentComponent from "./common/CommentComponent";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import $sso from "../commons/utils/sso.js";
import LINKTYPE from "../commons/utils/linktype.js";
import pim from "../commons/utils/pim.js";

require("swiper/dist/css/swiper.css");
export default {
  data() {
    return {
      api: this.$store.state.api,
      BASE_URL: BASE_URL,
      token: localStorage.getItem('access_token'),
      eightD: this.$route.query.eightD,
      categoryCnName: "",
      categoryEnName: "",
      cnName: "",
      color: "",
      imagePath: "",
      price: "",
      srp: "",
      pts: "",
      memberpriceflag: "",
      storestatus: "",
      salestatus: "",
      cartpromotelist: [],
      showInfo: "main",
      feature: "",
      mainSpec: [],
      swiperImgs: [],
      specifications: [],
      suit: [],
      foc: [],
      customizedLink: [],
      colorList: [],
      recommend: "",
      recommendList: [],
      averageRating: "",
      totalCount: "",
      reviews: [],
      showSelectBundle: false,
      isShowAddBasket: false,
      collapse: [{ collapsed: false }, { collapsed: false }],
      selectdBundle: {},
      quantity: 1,
      swiperOption: {
        notNextTick: true,
        autoplay: 3000,
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        pagination: { el: ".swiper-pagination" },
        paginationClickable: true,
        scrollbar: ".swiper-scrollbar",
        mousewheelControl: true,
        observeParents: true,
        mouseWheel: true,
        click: true,
        tap: true
      }
    };
  },
  created() {
    this.login();
    this.getMasterData(this.eightD, false);
    this.getDetail();
    this.getReviews();
    this.getDetailSpec();
    //this.getProductAccessory();
  },
  watch: {
    $route(to, from) {
      this.eightD = this.$route.query.eightD;
      this.getMasterData(this.eightD, false);
      this.getDetail();
      this.getReviews();
      this.getDetailSpec();
    },
    selectdBundle(val){
    return
      let url = this.api.product.findMasterData();
      var dArr = val.eightd.split(",");
      console.log(dArr)
      for(let i =0;i<dArr.length;i++){
        if(dArr[i]!=this.eightD){
           this.$http
           .get(url, {
             params: {
               eightD: dArr[i]
             }
           })
           .then(res => {
             if (res.data.code == "00" && res.data.result) {
               let info = res.data.returnData.OutMasterData;
               this.colorList.push(info);

            }
          })
        }
      }
    }
  },
  computed: {
    promotionTitle(_tit) {
      switch (_tit) {
        case 'ORDER_THRESHOLD_PROMO_CART':
          return '促销';
        case 'ORDER_THRESHOLD_PROMO_ALL':
          return '促销';
        case 'ORDER_THRESHOLD_PROMO_ANY':
          return '促销';
      }
    }
  },
  methods: {
    changeQuantity(_q) {
      if(_q>0&&_q<11) {
        this.quantity = _q;
      }
    },
    toggleCollapse(_i) {
      if (_i == "suit") {
        this.collapse[0].collapsed = !this.collapse[0].collapsed;
      } else if (_i == "foc") {
        this.collapse[1].collapsed = !this.collapse[1].collapsed;
      }
    },
    selectBundle() {
      this.showSelectBundle = !this.showSelectBundle;
    },
    ensureBundle() {
      this.showSelectBundle = false;
    },
    getBundle(_8d) {
      this.colorList = [];
      var dArr = _8d.split(",");
      this.eightD = dArr[0];
      for (var i = dArr.length - 1; i >= 0; i--) {
        this.getMasterData(dArr[i], false);
      }


      this.getDetail();
      this.getReviews();
      this.getDetailSpec();
    },
    getColor(_8d) {
      //this.colorList = [];
      this.eightD = _8d;
      this.getMasterData(_8d, true);

      this.getDetail();
      this.getReviews();
      this.getDetailSpec();
    },
    infinite(done) {
      if (this.showInfo != "main") {
        done();
        return;
      }
      console.log("infinite");
      this.showInfo = "feature";
      document.getElementsByClassName("_v-content")[0].style.transform =
        "translate3d(0px, 0px, 0px) scale(1)";
      done();
    },
    refresh(done) {
      console.log("refresh");
      this.showInfo = "main";
      done();
    },
    toggleInfo(info) {
      if (info == "compare") {
        this.$router.push({
          path: "/Compare",
          query: {
            eightD: this.eightD
          }
        });
      }
      this.showInfo = info;
      console.log(this.showInfo);
      document.getElementsByClassName("_v-content")[0].style.transform =
        "translate3d(0px, 0px, 0px) scale(1)";
    },
    getIframeHeight() {
      var featureHeight = document.documentElement.clientHeight;
      var iframeHeight = featureHeight - 140;
      document.getElementsByClassName("p-feature")[0].style.height =
        featureHeight + "px";
      document.getElementById("iframe").style.height = iframeHeight + "px";
    },

    ssologin() {
      // alert("Is Sony APP ");
      console.log("ssologin");
      $sso.login({ linkFrom: LINKTYPE.GOBACK });
    },

    login() {
      $sso.checkLogin();
    },
    getReviews() {
      let url = this.api.review.getProductReviews();
      this.$http
        .post(
          url,
          JSON.stringify({
            product8D: this.eightD
          })
        )
        .then(res => {
          if (
            res.data.resultMsg[0].code == "00" &&
            res.data.resultMsg[0].message == "Success"
          ) {
            var overallReviewStatus = res.data.resultData.overallReviewStatus;
            var reviews = res.data.resultData.reviews;

            if (overallReviewStatus) {
              this.averageRating = overallReviewStatus.averageRating;
              this.totalCount = overallReviewStatus.totalCount;
            }
            if (reviews) {
              this.reviews = reviews;
            }
          }
        });
    },
    async getMasterData(_8d, isColor) {
      var pd = await pim.getMasterWithPrice(_8d);

      if (pd && pd.sku8D) {
        if (!isColor) {
          this.colorList.push(pd);
        }

        this.cnName = pd.cnName;
        this.categoryCnName = pd.categoryCnName;
        this.categoryEnName = pd.categoryEnName;
        this.color = pd.color;
        this.imagePath = pd.imagePath;

        this.srp = pd.srp;
        this.price = pd.price;
        this.pts = pd.productprice.pts;
        this.memberpriceflag = pd.productprice.memberpriceflag;
        this.storestatus = pd.productprice.storestatus;
        this.salestatus = pd.productprice.salestatus;
        this.cartpromotelist = pd.cartpromotelist;
      }
    },

    async getDetailSpec(){
      let spec = await pim.getMainSpec(this.eightD);
      if(spec.OutSkuDetailSpec && spec.OutSkuDetailSpec.specLevelOnes){
        this.mainSpec = spec.OutSkuDetailSpec.specLevelOnes;
      }
    },

    async getDetail() {
      let pd = await pim.getProductDetail(this.eightD);
      if (pd && pd.hasDetail) {
        //this.feature = pd.feature?pd.feature:'https://www.sonystyle.com.cn/products/nex/ilce_6000_app.htm';
        this.recommend = pd.recommend;
        if(this.recommend) {
          this.recommendList=[];
           var reArr = this.recommend.split(',');
           reArr.forEach(val=>{
              this.getRecommend(val);
           })
        }
        this.swiperImgs = pd.gallery.items;
        this.specifications = pd.specifications;
        this.suit = pd.suit;
        this.foc = pd.foc;
        this.customizedLink = pd.customizedLink;
        if (pd.currentBundle) {
          this.selectdBundle = pd.currentBundle;
        }

      }
    },
    async getRecommend(val) {
      let r =await pim.getMasterWithPrice(val);
      console.log(r)
      this.recommendList.push(r);
    },
    getProductAccessory() {
      let url = this.api.product.findProductAccessory();

      this.$http
        .get(url, {
          params: {
            eightD: this.eightD
          }
        })
        .then(res => {
          if (res.data.code == "00" && res.data.result) {
          }
        })
        .catch(res => {});
    },
    showAddBasket() {
      this.isShowAddBasket = !this.isShowAddBasket;
    },
    addBasket() {
      this.isShowAddBasket = false;
      var product = [
        {
          product8D: this.eightD,
          quantity: this.quantity,
          mainProduct: "Y",
          relationType: "",
          parent8D: ""
        }
      ];
      let url = this.api.basket.addToBasket(this);
      this.$http
        .post(
          url,
          JSON.stringify({
            products: product
          })
        )
        .then(res => {
          if (
            res.data.resultMsg[0].code == "00" &&
            res.data.resultMsg[0].message == "Success"
          ) {
            alert("加入成功");
          } else if (res.data.resultMsg[0].message) {
            alert(res.data.resultMsg[0].message);
          }
        })
        .catch(res => {});
    },
    addWishList() {
      let url = this.api.wishlist.addToWishList(this);
      this.$http
        .post(
          url,
          JSON.stringify({
            product8D: this.eightD
          })
        )
        .then(res => {
          if (
            res.data.resultMsg[0].code == "00" &&
            res.data.resultMsg[0].message == "Success"
          ) {
            alert("加入成功");
          } else if (res.data.resultMsg[0].message) {
            alert(res.data.resultMsg[0].message);
          }
        })
        .catch(res => {});
    },
    goCart() {
      this.$router.push({ path: "/ShopCart" });
    },
    goAllComment() {
      this.$router.push({
        path: "/AllComment",
        query: {
          eightD: this.eightD
        }
      });
    }
  },
  components: {
    swiper,
    swiperSlide,
    CommentComponent
  }
};
</script>



