      <div v-if="!showEdit" class="shopcart-footer">
        <div v-if='isShowPromotion&&promotion_head&&promotion_head[1]' class="shopcart-notice">
          <p v-for='pro in promotion_head' v-if='pro.promotion.description'><span>促销</span>{{pro.promotion.description}}</p>
