<script>
import Popup from './common/Popup'
    export default{
        data() {
          return {
            api: this.$store.state.api,
            BASE_URL: BASE_URL,
            showCoupon: false,
            showEdit: false,
            checkedNo: [],
            cartId: '',
            mainList: [],
            itemList: [],
            dAddressNo: '',
            finalPrice: '',
            freight: '',
            totalQuantity: '',
            totalHeadDiscount: '',
            promotion_item: [],
            promotion_head: [],
            isShowPromotion: true,
            popupTitle: '',
            popupDes: '',
            popupType: 0,
            popupShow: false
          }
        },
        created() {
          this.cartId=localStorage.getItem("cartId");
          if(this.$route.query.code) {
            this.applyCoupon();
          }else {
            this.getCart();
          }

        },
        components: {
          Popup
        },
        computed: {
          totalDiscount() {
            var d = 0;
            for(var i=0;i<this.mainList.length;i++) {
              d+=this.mainList[i].discount/1;
            }
            return d+this.totalHeadDiscount/1;
          }
        },
        methods: {
          showFreight() {
            this.popupTitle = '运费说明'
            this.popupDes='单笔订单满200元可免运费;</br>最终运费已订单页面为准;';
            this.popupType=4;
            this.popupShow=true;
          },
          hidePopup() {
            this.popupShow=false;
          },
          closePromotion() {
            this.isShowPromotion = false;
          },
          selectCoupon(_8d) {
            let url=`${BASE_URL}/content/dam/sonystyle-club/index.html?productCode=${_8d}#/coupon`;
            window.location.href = url;
          },
          applyCoupon(){
            let url = this.api.coupon.applyCoupon(this);
            this.$http.post(url,{
              couponCode: this.$route.query.code,
              cartID: this.cartId
            })
            .then(res=>{
               this.getCart();
            })
            .catch(err=>{

            })
          },
          removeCoupon(_code) {
            let url = this.api.coupon.removeCoupon(this);
            this.$http.post(url,{
              couponCode: _code,
              cartID: this.cartId
            })
            .then(res=>{
              if(res.data.code=='00'){
                this.getCart();
              }
            })
          },
          editCart(info) {
            this.showEdit = !this.showEdit;
          },
           getCart() {
              let url = this.api.cart.readCart(this);
              this.$http.post(url,{})
              .then(res=>{
                if(res.data.resultMsg&&res.data.resultData){
                   this.itemList = res.data.resultData.itemList;
                   this.cartId=res.data.resultData.header.cartId;
                   localStorage.setItem("cartId",this.cartId);
                   this.dAddressNo = res.data.resultData.header.dAddressNo;
                   this.finalPrice = res.data.resultData.header.finalPrice;
                   this.freight = res.data.resultData.header.freight;
                   this.totalQuantity = res.data.resultData.header.totalQuantity;
                   this.totalHeadDiscount = res.data.resultData.header.totalHeadDiscount;
                   if(this.itemList) {
                    this.resolveList(this.itemList);
                   }else {
                     this.mainList = [];
                   }
                   this.getPromotion();
                }
              })
              .catch({

              })
           },
           pop() {
             if(this.checkedNo.length<1) {
              return;
             }
              this.popupDes='请问您确认将选中商品删除吗?';
              this.popupType=1;
              this.popupShow=true;
           },
           removeOrMove(_action) {
            switch(_action) {
              case 'moveAll':
                for(var i=0;i<this.checkedNo.length;i++){
                  this.moveWishlist(this.checkedNo[i],'','D');
                };
                break;
              case 'move':
                for(var i=0;i<this.checkedNo.length;i++){
                  this.moveWishlist(this.checkedNo[i],'','D');
                };
                break;
              case 'delete':
                for(var i=0;i<this.checkedNo.length;i++){
                  this.changeCart(this.checkedNo[i],'','D');
                };
                break;
              default:
                break;
            }
            this.hidePopup();
            this.checkedNo=[];
           },
           changeCart(_No,_quantity,_action) {
            if(_action=='U'&&_quantity<1){
              //this.popupTitle = '运费说明'
              this.popupDes='不能再减少咯^-^';
              this.popupType=4;
              this.popupShow=true;
              return;
            }
            let url = this.api.cart.changeCart(this);
            this.$http.post(url,JSON.stringify({
              entryNumber: _No,
              quantity: _quantity,
              actionType: _action,
            })
            ).then(res=>{
                if(res.data.resultMsg&&res.data.resultData){
                   this.itemList = res.data.resultData.itemList;
                   this.cartId = res.data.resultData.header.cartId;
                   this.dAddressNo = res.data.resultData.header.dAddressNo;
                   this.finalPrice = res.data.resultData.header.finalPrice;
                   this.freight = res.data.resultData.header.freight;
                   this.totalQuantity = res.data.resultData.header.totalQuantity;
                   this.totalHeadDiscount = res.data.resultData.header.totalHeadDiscount;
                   if(this.itemList){
                    this.resolveList(this.itemList);
                   }else {
                     this.mainList = [];
                   }
                }else if(res.data.resultMsg[0].code=='99'){
                  this.popupDes=res.data.resultMsg[0].message;
                  this.popupType=4;
                  this.popupShow=true;

                }
              })
              .catch({

              })
           },
           moveWishlist(_No) {
            let url = this.api.cart.moveToWishlist(this);
            this.$http.post(url,JSON.stringify({
              entryNumber: _No
            })
            ).then(res=>{
                if(res.data.resultMsg&&res.data.resultMsg[0].code=='00'){
                   this.getCart();
                }
              })
              .catch({

              })
           },
           getPromotion() {
            let url = this.api.promotion.getCartPromotions(this);
            this.$http.post(url,JSON.stringify({
              cartID: this.cartId
            }))
            .then(res=>{
              if(res.data.resultMsg[0].code=='00'&&res.data.resultMsg[0].message=='Success'){
                this.promotion_item = res.data.resultData.promotion_item;
                this.promotion_head = res.data.resultData.promotion_head;
              }
            })
            .catch({

            })
           },
            resolveList(data) {
                this.mainList = [];
                var url = this.api.product.findMasterData();
                for(let i=0;i<data.length;i++){
                    if(data[i].mainProduct=='Y'){
                        data[i].foc = [];
                        for(let l=0;l<data.length;l++){
                          let focItem = data[l];
                          if(data[l].mainProduct=='N' && data[i].product8D==data[l].parent8D){
                              this.$http.get(url,
                                {
                                  params: {
                                  'eightD': focItem.product8D
                                  }
                                }
                              ).then(res=>{
                                if(res.data.result && res.data.code=='00') {
                                  console.log(data[l].product8D+':'+res.data.returnData.OutMasterData.imagePath)
                                  focItem.imgURL = res.data.returnData.OutMasterData.imagePath;
                                  data[i].foc.push(focItem);
                                }
                              })

                          }
                        }
                        this.$http.get(url,
                          {
                            params: {
                            'eightD': data[i].product8D
                            }
                          }
                        ).then(res=>{
                          if(res.data.result && res.data.code=='00') {
                          console.log(data[i].product8D+':'+res.data.returnData.OutMasterData.imagePath)
                            data[i].imgURL = res.data.returnData.OutMasterData.imagePath;
                            this.mainList.push(data[i]);
                          }
                        })

                    }
                }
            },
            continueCheck() {
              this.$router.push({path:'/SubmitOrder'});
            },
            checkout() {
              let url = this.api.cart.checkout(this);
              this.$http.post(url)
              .then(res=>{
                if(res.data.resultMsg[0].code=='00'){
                   this.$router.push({path:'/SubmitOrder'});
                }else if(res.data.resultMsg[0].code=='01') {
                  this.popupDes='您选择的赠品中有缺货商品</br>选择继续结算,将不能更换赠品';
                  this.popupType=3;
                  this.popupShow=true;
                }
              })
              .catch(res=>{

              })
            }
        }
    }

</script>



